import axios from "axios";
import { ONLINE_API_KEY } from "../secret/online-secret";

export const onlineHeaders = {
    'content-type': 'application/json',
    'accept': 'application/json',

    'X-Requested-With': 'XMLHttpRequest',
    'X-API-KEY': ONLINE_API_KEY
};


const online = axios.create({

    baseURL: `https://movlar-dev.ru/api/v1`,
    // baseURL: `http://localhost:8000/api`,
    withCredentials: true,
    headers: onlineHeaders,
})
// online.defaults.redirect = "follow";


export const movlarAPI = {
 
    service: async (url, method, model, data) => {
        let result = null
        try {

            const response = await online[method](url, data)

            if (response && response.data) {
                if (response.data.resultCode === 0) {

                    let data = response.data
                    if (data.data) {
                        data = data.data
                    }

                    result = data[model]
                } else {

                }
            }

            return result
        } catch (error) {

            return result
        }
    }
}
