import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { AppDispatch, AppGetState } from "@/app/model/store";
import { EV_REPORT_PROP, EventReportSelectItem } from "@/entities/EventReport/type/event-report-type";
import { eventReportActions } from "@/entities/EventReport";
import { Requesite } from "../type/requesite-type";



//TYPES
export type RequesiteState = typeof initialState





export const initialState = {

    items: [] as Requesite[],
    current: null as Requesite,
    isFetched: false as boolean


}

const requesiteSlice = createSlice({
    name: 'requesite',
    initialState,
    reducers: {

        setFetchedRq: (
            state: RequesiteState,
            action: PayloadAction<{
                rqs: Requesite[],
            }>

        ) => {

            state.isFetched = true
            state.items = action.payload.rqs
            if (state.items.length) {
                state.current = action.payload.rqs[0]
            }

        },
        setFinishStatus: (
            state: RequesiteState,
            action: PayloadAction

        ) => {


        },
    },

});

export const requesiteReducer = requesiteSlice.reducer;

// Экспорт actions
export const requesiteActions = requesiteSlice.actions;

export function isDifferenceMoreThanFourMonths(inputDate: string): boolean {
    // Парсим переданную дату, если она строка
    const dateFromInput = new Date(inputDate);
    const currentDate = new Date();

    // Вычисляем разницу в месяцах
    const monthsDifference = (currentDate.getFullYear() - dateFromInput.getFullYear()) * 12
        + currentDate.getMonth() - dateFromInput.getMonth();

    // Сравниваем разницу
    return Math.abs(monthsDifference) > 4;
}

